@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono:wght@400;700&family=Ubuntu:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav.navbar{background-color:#e3350d;margin-bottom:3em}nav.navbar a.navbar-brand{color:white;font-weight:bold}nav.navbar div.navbar-collapse div.navbar-nav a.nav-link{color:whitesmoke}

.card-item{float:left;width:12rem;height:12rem;margin-bottom:1rem;transition:all .5s ease-in;box-shadow:0px 3px 5px rgba(0,0,0,0.25);color:black}.card-item div.card-body{padding:.5rem}.card-item div.card-body div.card-title{font-weight:bold;text-align:center}.card-item div.card-body div.card-subtitle{font-weight:300;color:gray}.card-item img{margin:0 auto;width:8rem;max-height:8rem;transition:all .5s ease}.card-item:hover{box-shadow:0px 10px 15px rgba(0,0,0,0.5)}.card-item:hover img{transform:scale(1.15)}

@use 'sass:math';.lds-grid{position:relative;width:96px;height:96px;margin:0 auto}.lds-grid div{position:absolute;width:32px;height:32px;border-radius:50%;background:#e3350d;animation:lds-grid 1.2s linear infinite}.lds-grid div:nth-child(1){top:16px;left:16px;animation-delay:0s}.lds-grid div:nth-child(2){top:56px;left:16px;animation-delay:-.4s}.lds-grid div:nth-child(3){top:96px;left:16px;animation-delay:-.8s}.lds-grid div:nth-child(4){top:16px;left:56px;animation-delay:-.4s}.lds-grid div:nth-child(5){top:56px;left:56px;animation-delay:-.8s}.lds-grid div:nth-child(6){top:96px;left:56px;animation-delay:-1.2s}.lds-grid div:nth-child(7){top:16px;left:96px;animation-delay:-.8s}.lds-grid div:nth-child(8){top:56px;left:96px;animation-delay:-1.2s}.lds-grid div:nth-child(9){top:96px;left:96px;animation-delay:-1.6s}@keyframes lds-grid{0%,100%{opacity:1}50%{opacity:0.5}}

table.table-bordered{border-radius:5px}span.bug{background:linear-gradient(180deg, #729f3f 50%, #729f3f 50%);background-color:#729f3f;color:#fff}span.dark{background:linear-gradient(180deg, #707070 50%, #707070 50%);background-color:#707070;color:#fff}span.dragon{background:linear-gradient(180deg, #53a4cf 50%, #f16e57 50%);background-color:#53a4cf;color:#fff}span.electric{background:linear-gradient(180deg, #eed535 50%, #eed535 50%);background-color:#eed535;color:#212121}span.fairy{background:linear-gradient(180deg, #fdb9e9 50%, #fdb9e9 50%);background-color:#fdb9e9;color:#212121}span.fighting{background:linear-gradient(180deg, #d56723 50%, #d56723 50%);background-color:#d56723;color:#fff}span.fire{background:linear-gradient(180deg, #fd7d24 50%, #fd7d24 50%);background-color:#fd7d24;color:#fff}span.flying{background:linear-gradient(180deg, #3dc7ef 50%, #bdb9b8 50%);background-color:#3dc7ef;color:#212121}span.ghost{background:linear-gradient(180deg, #7b62a3 50%, #7b62a3 50%);background-color:#7b62a3;color:#fff}span.grass{background:linear-gradient(180deg, #9bcc50 50%, #9bcc50 50%);background-color:#9bcc50;color:#212121}span.ground{background:linear-gradient(180deg, #f7de3f 50%, #ab9842 50%);background-color:#f7de3f;color:#212121}span.ice{background:linear-gradient(180deg, #51c4e7 50%, #51c4e7 50%);background-color:#51c4e7;color:#212121}span.normal{background:linear-gradient(180deg, #a4acaf 50%, #a4acaf 50%);background-color:#a4acaf;color:#212121}span.poison{background:linear-gradient(180deg, #b97fc9 50%, #b97fc9 50%);background-color:#b97fc9;color:#fff}span.psychic{background:linear-gradient(180deg, #f366b9 50%, #f366b9 50%);background-color:#f366b9;color:#fff}span.rock{background:linear-gradient(180deg, #a38c21 50%, #a38c21 50%);background-color:#a38c21;color:#fff}span.steel{background:linear-gradient(180deg, #9eb7b8 50%, #9eb7b8 50%);background-color:#9eb7b8;color:#212121}span.water{background:linear-gradient(180deg, #4592c4 50%, #4592c4 50%);background-color:#4592c4;color:#fff}

body{margin:0;font-family:'Ubuntu';-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:'Ubuntu Mono'}

